body {
  font: normal 125%/1.4;
  font-family: "Montserrat", sans-serif;
  font-size: 10px;
}

.card-container {
  position: absolute;
  color: white;
}
.card-container:hover {
  cursor: pointer;
}

.card-container {
  position: relative;
  width: 18rem;
  aspect-ratio: 0.7142857143;
  transform-style: preserve-3d;
  perspective: 1000px;
  font-size: 0.8rem;
}

.card__front {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 0.4rem;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  transform-style: preserve-3d;
  box-shadow: 20px 20px 10px rgba(15, 9, 9, 0.366);
  display: grid;
}
.card__front > * {
  grid-column: 1/2;
  grid-row: 1/2;
}
.card__front figure img {
  aspect-ratio: 0.7142857143;
  border-radius: 0.4rem;
}
.card__front .card__front-text {
  position: absolute;
  bottom: 5%;
  left: 0;
  transform: translateZ(10px);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.card__back {
  position: absolute;
  width: 100%;
  height: 100%;
  box-shadow: 20px 20px 10px rgba(15, 9, 9, 0.366);
  border-radius: 0.4rem;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  transform-style: preserve-3d;
}
.card__back .card__back-infolist {
  position: absolute;
  transform: translateZ(10px);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.card__front-text {
  display: grid;
  justify-content: center;
  position: relative;
  width: 100%;
}
.card__front-text h2 {
  position: absolute;
  top: -50%;
  left: 20%;
  transform: rotateZ(-90deg);
  transform-origin: left;
  font-size: 1.92rem;
  font-family: "Bangers", cursive;
  letter-spacing: 0.2rem;
  text-transform: uppercase;
  text-shadow: 3px 3px 10px rgb(25, 25, 25);
}
.card__front-text .card__front-stats {
  display: grid;
  grid-template-columns: repeat(2, 8rem);
  -moz-column-gap: 0.5rem;
       column-gap: 0.5rem;
  row-gap: 0.5rem;
  font-size: 1rem;
  font-weight: bold;
}

.card__back {
  background-color: rgb(44, 50, 66);
