}
.card__back .card__back-name h2 {
  font-size: 1.92rem;
  position: relative;
  font-family: "Rampart One", cursive;
}
.card__back .card__back-name h2:after {
  position: absolute;
  content: "";
  width: 20%;
  height: 0.1rem;
  background-color: white;
  bottom: -0.5rem;
  left: -35px;
}
.card__back .card__back-name h2:before {
  position: absolute;
  content: "";
  width: 0.1rem;
  height: 5rem;
  background-color: white;
  bottom: -5.5rem;
  left: -35px;
}
.card__back .card__back-infolist {
  -webkit-margin-before: 1.3333333333rem;
          margin-block-start: 1.3333333333rem;
