.navigation {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 100%;
  background-color: #24252a;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 1rem 3%;
  z-index: 3;
}
.navigation h2,
.navigation li,
.navigation a {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 1rem;
  color: #edf0f1;
  text-decoration: none;
}
.navigation .logo {
  cursor: pointer;
  margin-right: auto;
}
.navigation .nav__links li {
  display: inline-block;
  padding: 0 1rem;
}
.navigation .nav__links li .active {
  color: #ff8f00;
}
.navigation .nav__links li button {
  color: #edf0f1;
}
.navigation .nav__links li button .active {
  color: #edf0f1;
}
.navigation .nav__links li a {
  transition: all 0.3s ease 0s;
}
.navigation .nav__links li a:hover {
  color: #ff8f00;
}
.navigation button {
  margin-left: 0.5rem;
  padding: 0.5rem 1rem;
  background-color: #ff8f00;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}
.navigation button:hover {
  background-color: #edf0f1;
}
.navigation .nav__toggle {
  display: none;
}

@media (max-width: 768px) {
  .navigation {
    padding: 0;
    padding-left: 2rem;
  }
  .navigation .nav__links {
    position: absolute;
    top: 5rem;
    display: flex;
    flex-direction: column;
    background: #1c1d20;
    left: 0;
    width: 100%;
    transition: all 0.45s;
  }
  .navigation .nav__links li {
    margin: 1rem;
    transition: all 0.45s;
  }
  .navigation button {
    margin-left: 0rem;
  }
  .navigation .nav__toggle {
    display: flex;
    width: 5rem;
    height: 5rem;
    align-items: center;
    justify-content: center;
    transition: all 0.45s ease-in-out;
    cursor: pointer;
  }
  .navigation .nav__toggle .bar {
    position: relative;
    width: 2rem;
    height: 0.1rem;
    background: #edf0f1;
  }
}
