.navigation {
  position: sticky;
  top: 0;
  width: 100%;
  background-color: $nagivation-bg-color;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 1rem 3%;
  z-index: 3;

  h2,
  li,
  a {
    font-family: $font-text;
    font-weight: 500;
    font-size: $font-size-standart;
    color: $nagivation-link-color;
    text-decoration: none;
  }

  .logo {
    cursor: pointer;
    margin-right: auto;
  }

  .nav__links li {
    display: inline-block;
    padding: 0 1rem;

    .active {
      color: $nagivation-link-color-hover;
    }

    button {
      color: $nagivation-link-color;

      .active {
        color: $nagivation-link-color;
      }
    }

    a {
      transition: all 0.3s ease 0s;

      &:hover {
        color: $nagivation-link-color-hover;
      }
    }
  }

  button {
    margin-left: 0.5rem;
    padding: 0.5rem 1rem;
    background-color: $nagivation-bg-btn-color;
    border: none;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: all 0.3s ease 0s;

    &:hover {
      background-color: $nagivation-link-color;
    }
  }

  .nav__toggle {
    display: none;
  }
}

@media (max-width: $medium-screen) {
  .navigation {
    padding: 0;
    padding-left: 2rem;

    .nav__links {
      position: absolute;
      top: 5rem;
      display: flex;
      flex-direction: column;
      background: $nagivation-mobile-bg-color;
      left: 0;
      width: 100%;
      transition: all 0.45s;

      li {
        margin: 1rem;
        transition: all 0.45s;
      }
    }

    button {
      margin-left: 0rem;
    }

    .nav__toggle {
      display: flex;
      width: 5rem;
      height: 5rem;
      align-items: center;
      justify-content: center;
      transition: all 0.45s ease-in-out;
      cursor: pointer;

      .bar {
        position: relative;
        width: 2rem;
        height: 0.1rem;
        background: $nagivation-link-color;
      }
    }
  }
}
